  color: black;
}

.featured__hero__card:nth-of-type(2n) {
  margin-right: 0;
}

.featured__hero__card:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  box-shadow: 5px 10px 15px #a3a2a2;
}

.featured__hero__text {
  width: 19rem;
  height: 100%;
  position: absolute;
  padding: 75% 15px 15px 15px;
